      <span class="hljs-tag">&lt;<span class="hljs-name">md-button</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"!starting"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"md-raised md-accent"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"pause"</span>&gt;</span>{{pausing ? '继续' : '暂停'}}<span class="hljs-tag">&lt;/<span class="hljs-name">md-button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-button</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"md-raised md-accent"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"stop"</span>&gt;</span>结束<span class="hljs-tag">&lt;/<span class="hljs-name">md-button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
