<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="language-javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-title function_">data</span>(<span class="hljs-params"></span>) {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">minHeight</span>: -<span class="hljs-number">1</span>,
        <span class="hljs-attr">maxHeight</span>: <span class="hljs-number">7000</span>,
        <span class="hljs-attr">speed</span>: <span class="hljs-number">10</span>,
        <span class="hljs-attr">polygonHierarchy</span>: [
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">102.1</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">29.5</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">106.2</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">29.5</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">106.2</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">33.5</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">102.1</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">33.5</span> }
        ],
        <span class="hljs-attr">url</span>: <span class="hljs-string">'https://dev.virtualearth.net'</span>,
        <span class="hljs-attr">bmKey</span>: <span class="hljs-string">'AmGu3cvB_g1HbkQErEyvmLc9j0YIGWS7IdOqR7-hQbO8J92Fzrzkhy_bYKSsyoEx'</span>, <span class="hljs-comment">// 可到(https://www.bingmapsportal.com/)申请Key。</span>
        <span class="hljs-attr">pausing</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">starting</span>: <span class="hljs-literal">false</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
