<h2>属性</h2>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>polygonHierarchy</td>
<td>Array</td>
<td></td>
<td><code v-pre="">required</code> 指定构建淹没分析多边形的经纬度数组。</td>
</tr>
<tr>
<td>minHeight</td>
<td>number</td>
<td><code v-pre="">-1</code></td>
<td><code v-pre="">optional</code> 指定最小高程。</td>
</tr>
<tr>
<td>maxHeight</td>
<td>number</td>
<td><code v-pre="">8888</code></td>
<td><code v-pre="">optional</code> 指定最大高程。</td>
</tr>
<tr>
<td>speed</td>
<td>number</td>
<td><code v-pre="">10</code></td>
<td><code v-pre="">optional</code> 指定每帧增加的高度。</td>
</tr>
<tr>
<td>color</td>
<td>VcColor</td>
<td><code v-pre="">'rgba(40,150,200,0.6)'</code></td>
<td><code v-pre="">optional</code> 指定淹没分析对象颜色。</td>
</tr>
<tr>
<td>loop</td>
<td>boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> 指定到达最大高度后是否重新开始。</td>
</tr>
</tbody>
</table>
<hr>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>beforeLoad</td>
<td>(instance: VcComponentInternalInstance)</td>
<td>对象加载前触发。</td>
</tr>
<tr>
<td>ready</td>
<td>(readyObj: VcReadyObject)</td>
<td>对象加载成功时触发。</td>
</tr>
<tr>
<td>destroyed</td>
<td>(instance: VcComponentInternalInstance)</td>
<td>对象销毁时触发。</td>
</tr>
<tr>
<td>stop</td>
<td>(evt: Cesium.ClassificationPrimitive)</td>
<td>到达最大高度时触发。</td>
</tr>
</tbody>
</table>
<h2>方法</h2>
<table>
<thead>
<tr>
<th>方法名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>start</td>
<td>(height?: number) =&gt; void</td>
<td>开始淹没分析。</td>
</tr>
<tr>
<td>pause</td>
<td>() =&gt; void</td>
<td>暂停/继续淹没分析。</td>
</tr>
<tr>
<td>stop</td>
<td>() =&gt; void</td>
<td>结束淹没分析。</td>
</tr>
</tbody>
</table>
</body></html></section></template>

<script>
    export default {
      data () {
        return {
          minHeight: -1,
          maxHeight: 7000,
          speed: 10,
          polygonHierarchy: [
            {lng: 102.1, lat: 29.5},
            {lng: 106.2, lat: 29.5},
            {lng: 106.2, lat: 33.5},
            {lng: 102.1, lat: 33.5}
          ],
          url: 'https://dev.virtualearth.net',
          bmKey: 'AmGu3cvB_g1HbkQErEyvmLc9j0YIGWS7IdOqR7-hQbO8J92Fzrzkhy_bYKSsyoEx', // 可到(https://www.bingmapsportal.com/)申请Key。
          pausing: false,
          starting: false
        }
      },
      methods: {
        ready (cesiumInstance) {
          this.cesiumInstance = cesiumInstance
          window.vm = this
          const {Cesium, viewer} = this.cesiumInstance
          viewer.scene.globe.depthTestAgainstTerrain = true
          viewer.scene.debugShowFramesPerSecond = true
          viewer.camera.setView({
            destination: new Cesium.Cartesian3(-1432246.8223880068, 5761224.588247942, 3297281.1889481535),
            orientation: {
              heading: 6.20312220367255,
              pitch: -0.9937536846355606,
              roll: 0.002443376981836387
            }
          })
        },
        start (){
          this.$refs.flood.start()
          this.pausing = false
          this.starting = true
        },
        pause() {
          this.$refs.flood.pause()
          this.pausing = !this.pausing
        },
        stop() {
          this.$refs.flood.stop()
          this.pausing = false
          this.starting = false
        },
        onStoped(e) {
          this.pausing = false
          this.starting = false
          console.log(e)
        }
      }
    }
  </script>